<template>
  <div class="header__nav">
    <router-link v-for="route in routes" :key="route.name" :to="route.path">{{
      route.name
    }}</router-link>
  </div>
</template>
<script>
import style from "@/assets/style/Header/HeaderNav.scss";
export default {
  name: "HeaderNav",
  computed: {
    routes() {
      return this.$router.options.routes.filter(route => route.path.indexOf("*") === -1);
    },
  },
};
</script>

