import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/fashion/",
    name: "Home",
    meta: {layout: "MainLayout"},
    component: () =>
    import("../views/Home.vue")
  },
  {
    path: "/fashion/shop",
    name: "Shop",
    meta: {layout: "MainLayout"},
    component: () =>
    import("../views/Shop.vue")
  },
  {
    path: "/fashion/lookbook",
    name: "Lookbook",
    meta: {layout: "MainLayout"},
    component: () =>
    import("../views/Lookbook.vue")
  },
  {
    path: "/fashion/features",
    name: "Features",
    meta: {layout: "MainLayout"},
    component: () =>
    import("../views/Features.vue")
  },
  {
    path: "/fashion/pages",
    name: "Pages",
    meta: {layout: "MainLayout"},
    component: () =>
    import("../views/Pages.vue")
  },
  {
    path: "/fashion/blog",
    name: "Blog",
    meta: {layout: "MainLayout"},
    component: () =>
    import("../views/Blog.vue")
  },
  {
    path: "*",
    meta: {layout: "MainLayout"},
    component: () =>
    import("../views/Home.vue")
  },

];

const router = new VueRouter({
  mode: 'history',
  routes,
});

export default router;
