import Vue from "vue";
import Vuex from "vuex";
import images from "@/fixture/images.js";
import MensCloth from "@/fixture/MensCloth.js";
import WomensCloth from "@/fixture/WomensCloth.js";
import Links from "@/fixture/links.js";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    images,
    MensCloth,
    WomensCloth,
    Links,
  },
  mutations: {},
  actions: {},
  modules: {},
  getters: {
    getImages: (state) => {
      return state.images;
    },
    getMensCloth: (state) => {
      return state.MensCloth;
    },
    getWomensCloth: (state) => {
      return state.WomensCloth;
    },
    getLinks: (state) => {
      return state.Links;
    },
    getAvoneImages: (state) => {
      return state.images.slice(state.images.length - 3);
    }
  },
});
